import React, { useState } from 'react'
import classNames from 'classnames'
import { Drawer } from 'components/ui'
import { HiOutlineCog } from 'react-icons/hi'
import SidePanelContent from './SidePanelContent'
import withHeaderItem from 'utils/hoc/withHeaderItem'
import { setPanelExpand, setLang } from 'store/theme/themeSlice'
import { useSelector, useDispatch } from 'react-redux'

export const SidePanel = (props) => {

	const [language, setLanguage] = useState("CN");

	const dispatch = useDispatch()

	const { className, ...rest } = props
	
	console.log("The className " + className)

	const panelExpand = useSelector((state) => state.theme.panelExpand)

	const direction = useSelector((state) => state.theme.direction)

	const openPanel = () => {
		dispatch(setPanelExpand(true))
	}

	const closePanel = () => {
		dispatch(setPanelExpand(false))
		const bodyClassList = document.body.classList
		if (bodyClassList.contains('drawer-lock-scroll')) {
			bodyClassList.remove('drawer-lock-scroll', 'drawer-open')
		}
	}

	const changeLanguage = () => {
		if (language !== "CN") {
			setLanguage("CN")
			dispatch(setLang("CN"))
		}
		if (language === "CN") {
			setLanguage("EN")
			dispatch(setLang("EN"))
		}
	}

	return (
		<>
			<div onClick={changeLanguage}>{language}</div>
			{/* <div className={classNames('text-2xl', className)} onClick={openPanel} {...rest}>
				<HiOutlineCog />
			</div>
			<Drawer
				title="Theme Config"
				isOpen={panelExpand}
				onClose={closePanel}
				onRequestClose={closePanel}
				placement={direction === 'rtl' ? 'left' : 'right'}
				width={375}
			>
				<SidePanelContent callBackClose={closePanel} />
			</Drawer> */}
		</>
	)
}

export default withHeaderItem(SidePanel)
